	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-introducing-zaail-40-image-format-support-for-flash/index.md absPath of file >>> MarkdownRemark","html":"<p>This is something I've been waiting a while for!</p>\n<p><a href=\"http://www.zaalabs.com/2010/04/introducing-zaail-40-image-format-support-for-flash/\">http://www.zaalabs.com/2010/04/introducing-zaail-40-image-format-support-for-flash/</a></p>\n<p>Alchemy is a technology which makes it possible to convert code libraries from other languages, such as C, into ABC (Actionscript Byte Code) which is the underlying technology behind the Flash Player.  In other words, you can take code libraries from other places and use them in the Flash Player, without any additional dependencies.  It's not easy mind you, but it is very cool.</p>\n<p>I still haven't figured it out myself, so I've been impatiently waiting for someone else to be nice and figure it out for me.  Which they just have!</p>\n<p>What actually interests me the most about this is the fact that it adds .raw image support to the Flash Player, which is very much something that I'm interested in getting into <a href=\"/portfolio-2018/imagesizer\">ImageSizer</a>.  Expect the next major release to include .raw image support!</p>","frontmatter":{"title":"Link: Introducing ZaaIL – 40+ Image format support for Flash","date":"April 28, 2010","tags":["alchemy","flash-player"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/kick-ass-papervision-reflections/","tags":["flash","papervision-3d"],"title":"Kick-ass Papervision Reflections","image":null,"date":"2008-11-15T08:30:40.000Z"}},"next":{"frontmatter":{"path":"/blog/link-papervision3d-wormhole-in-your-webcam/","tags":["flash"],"title":"Link: Papervision3D wormhole in your webcam!","image":null,"date":"2009-02-19T12:26:11.000Z"}}}}

